.header__brand {
  font-family: var(--font-kufication);
}
.header__items {
  position: relative;
  margin-right: 0.9375em;
}
.header__items:last-child {
  margin-right: 0em;
}
.header__links {
  padding: 0 0.125em;
  letter-spacing: 0.0625em;
  text-decoration: none;
}
.header__items a::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: unset;
  right: 0;
  width: 0;
  height: 0.0208125em;
  transition: all 300ms;
  transition-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
}
.header__items a:hover::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: unset;
  width: 100%;
  height: 0.0208125em;
  background: var(--dark);
}
.header__items.function a::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: unset;
  width: 100%;
  height: 0.0208125em;
  background: var(--dark);
}
.header__feature-wrapper {
  margin-right: 0.625em;
}
.header__feature-wrapper:first-child {
  margin-right: 0.75em;
}
.header__feature-wrapper:last-child {
  margin-right: 0em;
}
.header__feature-wrapper svg {
  font-size: 1.125em;
}

.banner {
  margin-top: 4.375em;
}
.banner__content {
  padding-top: 7.5em;
}
.banner__content:last-of-type {
  padding-top: 1.875em;
}
.banner__sub-title {
  font-family: var(--font-signature);
  font-size: 1.125em;
  font-weight: bold;
}
.banner__sub-title::after {
  content: "";
  display: inline-block;
  height: 0.0625em;
  width: 20vw;
  margin-left: 0.625em;
  background: var(--dark);
}
.banner__title-name {
  font-family: var(--font-galaxus);
  font-size: 6.875em;
}
.banner__title-name span {
  font-family: var(--font-galaxus);
  color: var(--primary);
  display: block;
  margin: -0.4375em auto -0.25em auto;
  padding-left: 0.275em;
}
.banner__job-title {
  font-family: var(--font-memesique);
  font-size: 1.875em;
  letter-spacing: 0.0625em;
}
.banner__btn {
  padding-top: 1.25em;
}
.banner__button {
  color: var(--light);
  text-transform: uppercase;
  padding: 0.4375em 2em;
  background: var(--primary-background);
  border-radius: 0.3125em;
  transition: all 300ms linear;
  font-family: var(--font-titillium-semibold);
  font-size: 1.125em;
  letter-spacing: 0.0625em;
}
.banner__button:first-of-type {
  background: var(--secondary-background);
  margin-right: 0.7846875em;
}
.banner__button:hover {
  background: transparent;
  box-shadow: var(--btn-hover-boxshadow);
  color: var(--dark);
}
.banner__button:focus {
  background: var(--focus-background);
  box-shadow: var(--btn-focus-boxshadow);
  transform: scale(0.966);
}

.service {
  padding-top: 6.25em;
}
.service__content h2 {
  font-family: var(--font-galaxus);
  font-size: 2.5em;
  word-spacing: 0.075em;
  margin-bottom: -0.1875em;
}
.service__paragraph {
  font-family: var(--font-titillium-light);
  letter-spacing: 0.0625em;
  line-height: 1.6;
}

:root {
  --font-signature: "Signature";
  --font-spotyfied: "Spotyfied";
  --font-galaxus: "Galaxus";
  --font-mangaka: "Mangaka";
  --font-kufication: "Kufication";
  --font-memesique: "Memesique";
  --font-titillium-regular: "Titillium Regular";
  --font-titillium-light: "Titillium Light";
  --font-titillium-light-italic: "Titillium Light Italic";
  --font-titillium-bold: "Titillium Bold";
  --font-titillium-semibold: "Titillium Semi Bold";
  --primary: #0d6efd;
  --secondary: #6c757d;
  --success: #198754;
  --info: #0dcaf0;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #f8f9fa;
  --dark: #212529;
  --primary-background: linear-gradient(225deg, #ffd89b 10%, #19547b 70%);
  --secondary-background: linear-gradient(45deg, #ffd89b 10%, #19547b 70%);
  --focus-background: linear-gradient(45deg, #ffd89b 10%, #dc3545 70%);
  --btn-hover-boxshadow: rgba(0, 0, 0, 0.099) 6px 8px 8px 0px,
    rgba(0, 0, 0, 0.099) 6px 8px 8px 0px, rgba(0, 0, 0, 0.099) -6px 8px 8px 0px,
    rgba(0, 0, 0, 0.099) -6px 8px 8px 0px;
  --btn-focus-boxshadow: rgba(0, 0, 0, 0.099) 0px 8px 8px 0px,
    rgba(0, 0, 0, 0.099) 0px 8px 8px 0px;
  --card-boxshadow: rgba(0, 0, 0, 0.4) 0px 30px 90px;
  --box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

::-webkit-scrollbar {
  display: none;
}

::-webkit-scrollbar-thumb {
  display: none;
}

::-webkit-scrollbar-track {
  display: none;
}

*::selection {
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-drag: none;
}

* {
  outline: none;
  border: none;
}

*::before,
*::after {
  text-decoration: none;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

